import React, {useEffect, useState} from 'react';
import Title from "../../../components/title";
import downloadIcon from "../../../assets/icons/download.svg"
import dayjs from "dayjs";
import {useGetAllQuery} from "../../../hooks/api";
import {URLS} from "../../../constants/urls";
import {get} from "lodash";
import {OverlayLoader} from "../../../components/loader";
import Content from "../../../components/content";
import Section from "../../../components/section";
import {useTranslation} from "react-i18next";

const InpatientReportsByOrganizationsForm066ViewContainer = ({
                                                                 year = parseInt(dayjs().format('YYYY')),
                                                                 month = dayjs().format('MM'),
                                                                 form66_id
                                                             }) => {
    const {t} = useTranslation()
    const {data, isLoading} = useGetAllQuery({url: URLS.inpatientReports + `/${year}${month}/forms066/${form66_id}`})
    if (isLoading) {
        return <OverlayLoader/>
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-12">
                    <Title>{t('Форма 066')}</Title>
                </div>
                <div className="col-span-12 mt-8">
                    <Section>
                        <div className="grid grid-cols-12 gap-x-16">
                            <div className="col-span-12">
                                <Title
                                    className={'!text-2xl  mb-6'}>{t('Шифохонадан чиқарилганларнинг статистик картаси')}</Title>
                            </div>
                            <div className="col-span-6">

                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Беморнинг руйхатдан утиш рақами')}:</span>
                                    <strong className={'w-2/3'}>{get(data, 'data.id')}</strong>
                                </div>
                                <Title className={'!text-lg mt-3'}>{t('Шахсий маълумот')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('ЖШШИР')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.identifier[0].value')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Фамилияси')}:</span>
                                    <strong className={'w-2/3'}>{get(data, 'data.value.subject.name.lastName')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Исми')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.name.firstName')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Отасининг исми')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.name.patronymic')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Туғилган санаси')}:</span>
                                    <strong className={'w-2/3'}>{get(data, 'data.value.subject.birthDate')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Жинси')}:</span>
                                    <strong className={'w-2/3'}>{get(data, 'data.value.subject.gender')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Фуқаролиги')}:</span>
                                    <strong className={'w-2/3'}>{get(data, 'data.value.subject.citizenship')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Пасспорт ёки ИД')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.identifier[1].value')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">

                                <Title className={'!text-lg mt-3'}>{t('Яшаб келаётган манзили')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Вилоят')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Туман/Шаҳар')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].district')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Маҳалла')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].city')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Кўча')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].line')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Уй')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].line')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Почта индекси')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].postalCode')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Телефон(Уй)')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.telecom[0].value')}</strong>
                                </div>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Бемор бириктирилган муассаса')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.generalPractitioner.display')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Шифохонага ётқизиш ҳақида маълумот')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Тезкор ёрдам')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Шифохонада даволаниш')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Ётқизилган санаси ва вақти')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Беморнинг якуний ташхиси')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Асосий ташхис')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Паталагоанатомик ташхис')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Асосий ташхис')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Жаррохлик амалиётлари')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Асосий ташхис')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Қон анализ натижалари')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Асосий ташхис')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                            <div className="col-span-6">
                                <Title className={'!text-lg mt-3'}>{t('Маъсул шахслар')}</Title>
                                <div className={'flex py-3 border-b items-center text-sm'}>
                                    <span className={'w-1/3'}>{t('Асосий ташхис')}:</span>
                                    <strong
                                        className={'w-2/3'}>{get(data, 'data.value.subject.address[0].state')}</strong>
                                </div>
                            </div>
                        </div>
                    </Section>
                </div>
            </div>
        </div>
    );
};

export default InpatientReportsByOrganizationsForm066ViewContainer;